		padding: 50px 0 20px;
	}

	.section .heading {
		padding-bottom: 20px;
	}

	.section .heading .heading-bottom {
		margin-top: 10px;
	}

	/* ---------------------------------
	3. MENU
	--------------------------------- */

	header {
		top: 0;
		border-bottom: 1px solid rgba(255, 255, 255, 0.1);
	}

	header .logo {
		margin: 10px 0;
		height: 40px;
	}

	/* BOTTOM AREA */

	header .main-menu {
		text-align: left;
		display: none;
		float: none;
		clear: both;
		max-height: 400px;
		overflow: auto;
		border-top: 1px solid #ccc;
		background: #333;
	}

	header .main-menu > li {
		display: block;
		float: none;
		border-bottom: 1px solid #444;
	}

	header .main-menu > li:last-child {
		border-bottom: 0;
	}

	header .main-menu li > a {
		display: block;
		border: 0;
		line-height: 1;
		height: auto;
		padding: 15px;
		background: none;
	}

	/* DROPDOWN  */

	header .main-menu li.drop-down > ul.drop-down-menu {
		position: static;
		box-shadow: none;
	}

	header .main-menu li.drop-down > ul.drop-down-inner li:first-child {
		border-top: 1px solid #444;
	}

	/* DROPDOWN HOVER */

	header .main-menu li.drop-down > ul.drop-down-menu li a {
		padding-left: 25px;
	}

	header .main-menu li.drop-down > ul.drop-down-inner li a {
		padding-left: 35px;
	}

	/* NAV ICON */

	.menu-nav-icon {
		display: inline-block;
		float: right;
	}

	/* ---------------------------------
	7. WEDDING DETAILS AREA
	--------------------------------- */

	.wedding-details {
		margin-left: 15px;
		overflow: visible;
	}

	.wedding-details:after {
		left: 0;
		margin-left: 0px;
	}

	.w-detail {
		width: 100%;
		padding: 0 0 0 30px;
		margin: 0 0 30px;
	}

	.w-detail.left,
	.w-detail.right {
		text-align: left;
		float: none;
	}

	.w-detail.right:after,
	.w-detail.left:after {
		left: 0;
		margin-left: -9px;
	}

	/* ---------------------------------
	9. CONTACT AREA
	--------------------------------- */

	.contact-area .contact-wrapper {
		width: 100%;
		float: none !important;
	}

	.contact-area .map-area {
		height: 400px;
		width: 100%;
		position: static;
	}

	/* ---------------------------------
	10. VIEW GALLERY
	--------------------------------- */

	.galery-area .gallery-btn {
		margin-top: 10px;
	}

	/* ---------------------------------
	11. FOOTER
	--------------------------------- */

	footer ul > li > a {
		padding: 10px 7px;
	}

	footer .newsletter .email-area {
		width: 90%;
	}
}

